}

.load_cont_g {
    /* height: 150px;
    display: flex;
    flex-direction: column;
    text-align: center; */
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.325);
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
}

.load_cont_c {
    width: 35%;
    height: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(to right, #38b2ac, #63b3ed);
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease-in-out;
    animation: fadeIn 0.5s ease-in-out;
}

.load_cont_c:hover {
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
    transform: translateY(-2px);
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

